@media only screen and (max-width: 780px) {
  .movies {
    padding: 10px;
    margin-bottom: 10px;
  }
  .movies .movie {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
  }
  .movie__genres li {
    display: none;
  }
  .movie img {
    width: 50px;
    top: 0px;
    /* display: none; */
  }
  .movie_title {
    margin-bottom: 5px;
    font-size: 1em;
    color: #2c2c2c;
  }
  .movie__summary {
    font-size: 0.7em;
  }
  .movie .movie__column {
    width: 80%;
    font-size: 0.8em;
  }
}
